.font-bebas {
  font-family: 'Bebas Neue', sans-serif;
}

/* Ensure video covers full hero */
#hero video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translate(-50%, -50%);
}

/* Line clamp utility */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Smooth scroll */
html {
  scroll-behavior: smooth;
}

/* Selection color */
::selection {
  background: rgba(255, 107, 0, 0.3);
  color: white;
}

/* Focus states */
:focus-visible {
  outline: 2px solid #FF6B00;
  outline-offset: 2px;
}

/* Button hover states - ensure no blue/purple defaults */
button:focus {
  outline: none;
}

/* Carousel styling overrides */
.embla__container {
  display: flex;
}

.embla__slide {
  flex: 0 0 100%;
  min-width: 0;
}

/* Animation delays */
.delay-100 { animation-delay: 100ms; }
.delay-200 { animation-delay: 200ms; }
.delay-300 { animation-delay: 300ms; }
.delay-500 { animation-delay: 500ms; }
.delay-700 { animation-delay: 700ms; }
.delay-1000 { animation-delay: 1000ms; }

/* Hover card lift effect */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px rgba(255, 107, 0, 0.15);
}

/* Gradient text animation */
@keyframes gradient-text {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.animate-gradient-text {
  background-size: 200% 200%;
  animation: gradient-text 3s ease infinite;
}

/* Responsive typography */
@media (max-width: 640px) {
  .font-bebas {
    letter-spacing: 0.02em;
  }
}

/* Custom scrollbar for modals */
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(255, 107, 0, 0.5);
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 107, 0, 0.7);
}

/* Animacija loga: slide-in + float + glow */
.logo-animate {
  animation: slide-in-up-logo 0.6s ease-out forwards,
             float-logo 3s ease-in-out infinite,
             glow-logo 2s ease-in-out infinite;
}

/* ================================
   FLIP CARD FIX (Mobile + Long text)
   ================================ */

.flip-card {
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.7s ease;
}

/* Desktop hover */
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

/* Mobile tap (JS) */
.flip-card .flip-card-inner.is-flipped {
  transform: rotateY(180deg);
}

/* Both faces */
.flip-card-front,
.flip-card-back {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
}

/* Back face */
.flip-card-back {
  transform: rotateY(180deg) translateZ(0);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Long content scroll */
.flip-card-back .flip-scroll {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding-right: 6px;
}

/* scrollbar inside flip */
.flip-card-back .flip-scroll::-webkit-scrollbar {
  width: 6px;
}

.flip-card-back .flip-scroll::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.25);
  border-radius: 6px;
}

/* Mobile readability */
@media (max-width: 640px) {
  .flip-card-back .flip-scroll {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}

/* =========================
   SOCIAL ICONS – HERO ONLY
   (desktop left, mobile under CTA)
   ========================== */

/* Desktop shown by default */
.social-icons.desktop {
  position: absolute;
  left: 2rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  z-index: 50;
}

/* Mobile hidden by default (so it NEVER shows on desktop) */
.social-icons.mobile {
  display: none;
  margin-top: 2rem;
  justify-content: center;
  gap: 1rem;
}

.social-icon {
  position: relative;
  width: 52px;
  height: 52px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  backdrop-filter: blur(6px);
  animation: float 3.5s ease-in-out infinite;
}

/* Ping dot */
.social-icon::before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  top: -4px;
  right: -4px;
  animation: ping 1.5s cubic-bezier(0, 0, 0.2, 1) infinite;
}

/* Instagram = first icon */
.social-icons .social-icon:nth-child(1) {
  background: linear-gradient(
    135deg,
    rgba(147, 51, 234, 0.35),
    rgba(236, 72, 153, 0.35)
  );
  border: 1px solid rgba(236, 72, 153, 0.5);
  box-shadow: 0 0 25px rgba(236, 72, 153, 0.35);
}

.social-icons .social-icon:nth-child(1)::before {
  background-color: #ec4899;
}

/* TikTok = second icon */
.social-icons .social-icon:nth-child(2) {
  background: linear-gradient(
    135deg,
    rgba(8, 145, 178, 0.35),
    rgba(34, 211, 238, 0.35)
  );
  border: 1px solid rgba(34, 211, 238, 0.5);
  box-shadow: 0 0 25px rgba(34, 211, 238, 0.35);
}

.social-icons .social-icon:nth-child(2)::before {
  background-color: #06b6d4;
}

/* Icon size */
.social-icon svg {
  width: 24px;
  height: 24px;
  color: white;
}

/* Breakpoint: <=768px hide desktop, show mobile */
@media (max-width: 768px) {
  .social-icons.desktop {
    display: none;
  }
  .social-icons.mobile {
    display: flex;
  }
}

/* Animations */
@keyframes ping {
  75%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}
